.episode__card div {
}

.card-ep {
	display: flex;
	flex-wrap: nowrap;
	align-items: center;
	width: 100%;
	margin-bottom: 20px;
	padding: 8px 15px;
	line-height: 150%;
	border-radius: 10px;
	background: #3b3e43;
}

.card-ep__number {
	flex: 1 auto 10%;
	text-align: center;
}
.card-ep__info {
	flex: 1 auto 60%;
	margin: 0 15px;

}
.card-ep__name {
	font-size: 20px;
	font-weight: bold;
	cursor: pointer;
}
.card-ep__name:hover {
	color: #ff9800;
}
.card-ep__definition {
}
.card-ep__date {
	flex: 1 auto 30%;
	text-align: center;

}