.transition-element {
	transform: translateY(3vh);
  transition: opacity .3s, transform .3s;
}

.transition-element-enter {
	opacity: 0;
}

.transition-element-enter-active {
	opacity: 1;
	transform: translateY(0);
  transition: opacity .3s, transform .3s;
}

.transition-element-enter-done {
	opacity: 1;
	transform: translateY(0);
}